import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { StudentsService } from '../../services/students.service'
import { Observable } from 'rxjs/Observable';
import { IStudent } from '../../models/student.model'
import { Band } from '../../models/band.model'

/**
 * Generated class for the HomePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-home',
  templateUrl: 'home.html',
})
export class HomePage {

  studentsList$: Observable<IStudent[]>;
  //bandsList$: Observable<Band[]>;

  // band: Band = {
  //   name: ''
  // }

  constructor(public navCtrl: NavController, public navParams: NavParams,
    private songsService: StudentsService) {
    this.studentsList$ = this.songsService.getStudentList().snapshotChanges().map(changes => {
      return changes.map(c => ({
        key: c.payload.key, ...c.payload.val()
      }));
    });
  }

  ionViewWillLoad() {
    // this.bandsList$ = this.songsService.getBandList().snapshotChanges().map(changes => {
    //   return changes.map(c => ({
    //     key: c.payload.key, ...c.payload.val()
    //   }));
    // });
  }

  onContextChange(ctxt: string): void {
    // this.studentsList$ = this.songsService.assembleBandFilteredList(ctxt).snapshotChanges().map(changes => {
    //   return changes.map(c => ({
    //     key: c.payload.key, ...c.payload.val()
    //   }));
    // });
  }

  showAllSongs() {
    this.studentsList$ = this.songsService.getStudentList().snapshotChanges().map(changes => {
      return changes.map(c => ({
        key: c.payload.key, ...c.payload.val()
      }));
    });
  }

}



